
const routes = [
  /* NOTE 2023-12-08: 使用 () => import(...) 懒加载，即需要时才会被动态加载 */
  /* NOTE 2023-12-08: @ 是 /src 的化名 */

  { path: '/', component: () => import('@/pages/user/LoginPage.vue') },

  /* 管理员端 */
  {
    path: '/admin/home', component: () => import('@/pages/admin/HomePage.vue'), children: [
      /* 账号管理模块 */
      { path: '/admin/account/admin/register', component: () => import('@/pages/admin/account/admin/RegisterPage.vue') },
      { path: '/admin/account/teacher/register', component: () => import('@/pages/admin/account/teacher/RegisterPage.vue') },
      { path: '/admin/account/student/register', component: () => import('@/pages/admin/account/student/RegisterPage.vue') },

      /* 课程管理模块 */
      { path: '/admin/course/pending_course_list', component: () => import('@/pages/admin/course/approval/PendingListPage.vue') },
      { path: '/admin/course/not_pending_course_list', component: () => import('@/pages/admin/course/approval/NotPendingListPage.vue') },
      { path: '/admin/course/info', component: () => import('@/pages/admin/course/CourseInfoPage.vue') },
    ],
  },

  /* 教师端 */
  { 
    path: '/teacher/home', component: () => import('@/pages/teacher/HomePage.vue') ,
    children:[
      {path: '/teacher/home', component: () => import('@/pages/teacher/MainPage.vue')} ,
      { path: '/teacher/apply_for_course', component: () => import('@/pages/teacher/ApplyForCourse.vue') },
      { path: '/teacher/giveScoreForm', component: () => import('@/pages/teacher/GiveScoreForm.vue') },
      { path: '/teacher/giveScore', component: () => import('@/pages/teacher/GiveScore.vue') },
      { path: '/teacher/courses', component: () => import('@/pages/teacher/CheckCourse.vue') },
      { path: '/teacher/course_detail', component: () => import('@/pages/teacher/CourseDetail.vue') },
      { path: '/teacher/applications', component: () => import('@/pages/teacher/CheckApplication.vue') },
      { path: '/teacher/application_detail', component: () => import('@/pages/teacher/ApplicationDetail.vue') },
      { path: '/teacher/scoreAnalyse', component: () => import('@/pages/teacher/ScoreAnalyse.vue') },
    ]
  },

  /* 学生端 */
  /* 要是想让页面中的RouteView显示页面，需要添加children */
  { path: '/student/home', component: () => import('@/pages/student/HomePage.vue'),
    children:[
      { path: '/student/home/mainPage', component: () => import('@/pages/student/MainPage.vue') },
      { path: '/student/home/selectCoursePage', component: () => import('@/pages/student/SelectCoursePage.vue') },
      { path: '/student/home/courseManagePage', component: () => import('@/pages/student/CourseManagePage.vue') },
      {path: '/student/home/courseTablePage', component: () => import('@/pages/student/CourseTable.vue') },
      { path: '/student/home/selfInfoPage', component: () => import('@/pages/student/SelfInfoPage.vue') }
    ] 
  },
]

import { createRouter, createWebHashHistory } from 'vue-router'
const router = createRouter({ history: createWebHashHistory(), routes })

export default router
